.titleContainer {
  margin-right: auto;
  margin-left: auto;
  margin-top: 15%;
  width: 80%;
}

.container {
  height: 100vh;
}

.background {
  transition: all 300ms ease-in-out;
  background-color: #131313;
  position: absolute;
  border-radius: 0;
  z-index: -1;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
}

.backgroundScroll {
  transition: all 300ms ease-in-out;
  background-color: #131313;
  position: absolute;
  border-radius: 20px;
  z-index: -1;
  bottom: 50px;
  right: 50px;
  left: 50px;
  top: 50px;
}

.white {
  color: white;
}

.grey {
  color: #9b9b9b;
}

.text {
  color: #9b9b9b;
  margin: 10px 0 10px 0;
}

.blue {
  color: #8982ff;
}
