h1 {
  font-size: 40px;
  margin: 0px;
}

h2 {
  font-size: 30px;
  margin: 0;
}

h3 {
  font-size: 20px;
  margin: 0;
}
h4 {
  font-size: 15px;
  margin: 0;
}

p,
a {
  font-size: 15px;
  margin: 0;
  line-height: 40px;
}

@media screen and (min-width: 1400px) {
  h1 {
    font-size: 60px;
  }
  h2 {
    font-size: 50px;
  }
  h3 {
    font-size: 40px;
  }

  h4 {
    font-size: 25px;
    margin: 0;
  }

  p,
  a {
    font-size: 20px;
  }
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 20px;
    margin: 0px;
  }

  h2 {
    font-size: 15px;
    margin: 0;
  }

  h3 {
    font-size: 15px;
    margin: 0;
  }
  h4 {
    font-size: 10px;
    margin: 0;
  }
}
